export default {
  data: function () {
    return {
      name: '',
      desc: '',
      yt_id: '',
      mv_id: '',
      length: '',
      category: '',
      tag1: '',
      tag2: '',
      tag3: '',
      movie_data: [],
      movie_serach: [],
      query: '',
      categoryttl: '',
      page: 1,
      week: 7 * 24 * 60 * 60 * 1000,
      current_page: 1,
      input: '',
      today : new Date(),
      parPage: 15,
      tags: [{
          tag: '手書き風',
          param: 'handfree'
        },
        {
          tag: 'フィルター',
          param: 'filter'
        },
        {
          tag: '日常',
          param: 'days'
        },
        {
          tag: 'カウントダウン',
          param: 'countdown'
        },
      ],
           categories: [{
          "id": "youtube",
          "name": "youtube",
        },
        {
          "id": "wedding",
          "name": "結婚式"
        },
        {
          "id": "smartphone",
          "name": "スマホサイズ"
        },
        {
          "id": "business",
          "name": "ビジネス"
        },
      ],    
    }
  },
  created: function () {
    let self = this
    this.setQuery()
    this.$store.dispatch('movies/init');
    this.$store.dispatch('movies/getCategory', this.query).then(querySnapshot => {
      querySnapshot.forEach(doc => {
        self.movie_serach.push(doc.data());
      });
    })
    this.setCategories();
  },
  methods: {
    remove(id) {
      this.$store.dispatch('movies/remove', id)
    },
    setQuery() {
      this.query = this.$route.params.category || ''
    },
    setCategories() {
      this.categories.forEach((category) => {
        if (category.id == this.query) {
          this.categoryttl = category.name
        }
      })
    },
    download(mv_id, id, count) {
      let mp4 = mv_id + '.mp4';
      this.$store.dispatch('movies/download', mp4).then(url => {
        const xhr = new XMLHttpRequest();
        xhr.responseType = 'blob';
        xhr.onload = event => {
          // blobがファイルのデータです
          const blob = xhr.response;
          // aタグをつくります※この辺は自由にアプリに合わせてください
          const aDL = document.createElement('a');
          // ファイルデータに紐づくダウンロードリンクを設定します
          aDL.href = URL.createObjectURL(blob);
          aDL.download = `${mv_id}.mp4`;
          console.log(aDL);
          aDL.click();
        }
        xhr.open('GET', url);
        xhr.send();
      });
    }
  },
  computed: {
    movies() {
      return this.$store.state.movies.movies;
    },
    limitCount() {
      return this.movie_serach.slice(0, 3)
    },
     getMovies() {
      let current = this.current_page * this.parPage;
      let start = current - this.parPage;
      return this.movie_serach.slice(start, current)
    },      
    getPageCount() {
      return Math.ceil(this.movie_serach.length / this.parPage)
    }
  },
  watch: {
    search() {
      this.current_page = 1
    }
  }
}

